<template>
  <el-form
    :model="form"
    :rules="rules"
    hide-required-asterisk
    size="small"
    class="clearfix"
    ref="form">
    <el-form-item prop="companyName" :label="$t('order-meeting.intention.company_name')">
      <el-input v-model="form.companyName" />
    </el-form-item>

    <el-form-item prop="receiverAddress" :label="$t('order-meeting.intention.shipping_address')">
      <el-input v-model="form.receiverAddress" />
    </el-form-item>

    <el-form-item prop="phone" :label="$t('order-meeting.intention.telephone')">
      <el-input v-model="form.phone" />
    </el-form-item>

    <el-form-item prop="email" :label="$t('order-meeting.intention.email')">
      <el-input v-model="form.email" />
    </el-form-item>
  </el-form>
</template>

<script>
import { validateFormFields } from '@/utils/validators/rules-validation';
import i18n from 'i18next';

export default {
  name: 'ClientInfoForm',
  data() {
    return {
      form: {
        companyName: '',
        receiverAddress: '',
        phone: '',
        email: '',
      },
      rules: {
        companyName: [
          { required: true, message: i18n.t('order-meeting.intention.not_empty') },
          { pattern: /(^\S.*\S$)|^\S$/, message: i18n.t('order-meeting.intention.not_allow_space') },
          { min: 2, message: i18n.t('order-meeting.intention.under_minimum_charater', ['2']) },
          { max: 30, message: i18n.t('order-meeting.intention.exceeds_charater_limit', ['30']) },
        ],
        receiverAddress: [
          { required: true, message: i18n.t('order-meeting.intention.not_empty') },
          { pattern: /(^\S.*\S$)|^\S$/, message: i18n.t('order-meeting.intention.not_allow_space') },
          { min: 2, message: i18n.t('order-meeting.intention.under_minimum_charater', ['2']) },
          { max: 50, message: i18n.t('order-meeting.intention.exceeds_charater_limit', ['50']) },
        ],
        phone: [
          { required: true, message: i18n.t('order-meeting.intention.not_empty') },
          { pattern: /(^\S.*\S$)|^\S$/, message: i18n.t('order-meeting.intention.not_allow_space') },
          { pattern: /^[0-9-\s]+$/, message: i18n.t('order-meeting.intention.only_contain_digits_and_hyphen') },
          { min: 6, message: i18n.t('order-meeting.intention.under_minimum_charater', ['6']) },
          { max: 30, message: i18n.t('order-meeting.intention.exceeds_charater_limit', ['30']) },
        ],
        email: [
          { required: true, message: i18n.t('order-meeting.intention.not_empty') },
          { pattern: /(^\S.*\S$)|^\S$/, message: i18n.t('order-meeting.intention.not_allow_space') },
          { type: 'email', message: i18n.t('order-meeting.intention.not_a_valid_email_address') },
          { max: 50, message: i18n.t('order-meeting.intention.exceeds_charater_limit', ['50']) },
        ],
      },
    };
  },
  methods: {
    async validateForm() {
      Object.keys(this.form).forEach((prop) => {
        if (typeof this.form[prop] === 'string') {
          // 去掉首尾空格
          // 防止XSS攻击（html方面）
          this.form[prop] = this.form[prop]
            .trim()
            .replace(/</g, '&lt;')
            .replace(/>/g, '&gt;');
        }
      });

      try {
        await validateFormFields(this.$refs.form);
        this.$emit('validate', this.form);
        return this.form;
      } catch (err) {
        console.warn(err);
        throw new Error(i18n.t('order-meeting.intention.please_check_for_missing_or_incomplete_information'));
      }
    },
    resetFields() {
      this.$refs.form.resetFields();
    },
  },
};
</script>

<style lang="scss" scoped>
.el-form {
  margin-right: -120px;
  .el-form-item {
    float: left;
    width: 540px;
    margin-right: 120px;
  }
}
/deep/ .el-input__inner {
  border: 1px solid #979797;
  border-radius: 0;
}
/deep/ .el-form-item__label {
  color: #03234c;
}
</style>
