<template>
  <div class="shoe-item-container">
    <div class="table-field-1">
      <BaseImage
        class="shoe-picture"
        :src="data.curtailImg"
        fit="contain"
        @click="navigatToDetail" />
    </div>

    <div class="table-field-2 shoe-info">
      <p @click="navigatToDetail" class="shoe-name">{{ data.shoeName }}</p>
      <p class="shoe-color">{{$t('order-meeting.intention.color')}}：{{ data.colorName }}</p>
      <div class="flex">
        <p class="shoe-label">
          {{$t('order-meeting.intention.porduct_number')}}：{{ data.barCode }}</p>
        <p class="shoe-size">
          {{$t('order-meeting.intention.shoe_size')}}：{{ data.shoeSize | array }}</p>
      </div>
    </div>

    <div class="table-field-3 shoe-number">
      <el-input-number
        v-model="data.shoeNumber"
        :min="1"
        :max="1000000000"
        :step="1"
        size="small"
        :label="$t('order-meeting.intention.description')"
        @change="changeNumber" />
    </div>

    <!-- 操作 -->
    <div class="table-field-4 shoe-delete">
      <p class="cursor-pointer" @click="deleteShoe">{{$t('order-meeting.intention.delete')}}</p>
    </div>
  </div>
</template>

<script>
import BaseImage from '@/components/base/Image';

export default {
  name: 'IntentionShoeItem',
  components: { BaseImage },
  props: {
    data: {
      type: Object,
      required: true,
    },
  },
  filters: {
    array(str) {
      return str.split(/[,，;；\s]/).join(' ');
    },
  },
  data() {
    return {
      number: 0,
    };
  },
  methods: {
    changeNumber(currentVal, oldVal) {
      if (currentVal && oldVal) this.$emit('change-number', { currentVal, oldVal });
    },
    deleteShoe() {
      this.$emit('delete', this.data.shoeNumber);
    },
    navigatToDetail() {
      const { catalogId, collection, colorId } = this.data;
      const routeData = this.$router.resolve({
        name: 'OrderMeetingDetail',
        query: {
          workspaceId: this.$route.query.workspaceId,
          collocationId: colorId,
          catalogId,
          collection,
        },
      });

      window.open(routeData.href, '_blank');
    },
  },
};
</script>

