<template>
  <div class="intention-unsubmit-container">
    <!-- 基本信息 -->
    <section class="form-base">
      <h2 class="section-title">{{$t('order-meeting.intention.base_info')}}</h2>
      <client-info-form ref="clientForm" />
    </section>

    <!-- 鞋款系列详情 -->
    <section class="shoe-series">
      <!-- 表单表头 -->
      <ul class="series-table-header">
        <li class="table-field-1"></li>
        <li class="table-field-2">{{$t('order-meeting.intention.product_info')}}</li>
        <li class="table-field-3">{{$t('order-meeting.intention.product_quantity')}}</li>
        <li class="table-field-4">{{$t('order-meeting.intention.operation')}}</li>
      </ul>

      <!-- 表单内容 -->
      <div v-if="!total" class="placeholder-empty">
        <img
          src="@/assets/img/fila/placeholder-empty-intention.png"
          alt="placehoder When intention order is empty"
          class="placeholder-image">
        <p class="placeholder-text">{{$t('order-meeting.intention.empty_list')}}</p>
      </div>
      <ul v-else>
        <li v-for="(series, i) in list" :key="i">
          <h3 v-if="series.shoeDetails.length" class="series-title">{{ series.collectionName }}</h3>
          <transition-group appear name="flipOutX" tag="ul">
        <li v-for="shoe in series.shoeDetails" :key="shoe.wishId">
          <ShoeItem
            :data="shoe"
            @change-number="handleChangeNumber(shoe.wishId, $event)"
            @delete="handleDeleteShoe(shoe.wishId, $event)" />
        </li>
        </transition-group>
        </li>
      </ul>
    </section>

    <!-- 提交意向单 -->
    <section v-if="total" class="submit-section">
      <p>
        {{$t('order-meeting.intention.total')}}：
        {{ tweenedNumber.toFixed(0) | number }} {{$t('order-meeting.intention.units')}}
      </p>
      <base-button
        mode="dark"
        style="width: 165px; height: 64px;font-size: 20px;"
        @click="submit">{{$t('order-meeting.intention.submit_intention')}}</base-button>
    </section>
  </div>
</template>

<script>
import Vue from 'vue';

import i18n from 'i18next';

import BaseButton from '@/views/order-meeting/components/BaseButton';
import mixinLoading from '@/mixins/loading';
import {
  getWishList,
  changeWishItemNumber,
  deleteWishList,
  sumbitWishList,
} from '@/api/order-meeting';
import ClientInfoForm from './components/ClientInfoForm';
import ShoeItem from './components/ShoeItem';
import DialogSubmitConfirm from './components/DialogSubmitConfirm';
import DialogDeleteConfirm from './components/DialogDeleteConfirm';


export default Vue.extend({
  name: 'IntentionUnsubmit',
  components: {
    ClientInfoForm,
    ShoeItem,
    BaseButton,
  },
  mixins: [mixinLoading],
  filters: {
    number(num) {
      const reg = /\d{1,3}(?=(\d{3})+$)/g;
      return (`${num}`).replace(reg, '$&,');
    },
  },
  data() {
    return {
      list: [],
      total: 0,
      tweenedNumber: 0,
    };
  },
  async created() {
    try {
      this.list = (await getWishList(this.$route.query.workspaceId)).data.shoeList;

      this.list.forEach(series => {
        series.shoeDetails.forEach(({ shoeNumber }) => {
          this.total += shoeNumber;
        });
      });
    } catch (err) {
      this.$errHandler(err);
    }
  },
  methods: {
    // 处理意向单列表中某个鞋款数量变化，同步累计到共计
    async handleChangeNumber(wishId, { currentVal, oldVal }) {
      const compareNum = currentVal - oldVal;
      this.total += compareNum;

      try {
        await changeWishItemNumber(wishId, currentVal, this.$route.query.workspaceId);
      } catch (err) {
        this.$errHandler(err);
      }
    },
    // 处理在意向单列表中删除某个鞋款
    async handleDeleteShoe(wishId, shoeNumber) {
      // 弹出再次确认询问对话框
      const isConfirm = await this.$Dialog.dialogify({
        is: DialogDeleteConfirm,
      }, {
        props: {
          titleAlign: 'left',
          width: '467px',
          title: i18n.t('order-meeting.intention.warning'),
        },
      });

      if (isConfirm) {
        this.total -= shoeNumber;

        this.list.forEach(series => {
          series.shoeDetails.forEach((shoe, i) => {
            if (shoe.wishId === wishId) series.shoeDetails.splice(i, 1);
          });
        });

        try {
          await deleteWishList([wishId], this.$route.query.workspaceId);
        } catch (err) {
          this.$errHandler(err);
        }
      }
    },
    // 提交表单事件
    async submit() {
      try {
        // 校验表单是否通过
        const form = await this.$refs.clientForm.validateForm();

        // 根据接口的需要，更改意向单中鞋款数量的数据结构，
        const shoeList = [];
        this.list.forEach(series => {
          series.shoeDetails.forEach(({ wishId, shoeNumber }) => {
            shoeList.push({ wishId, shoeNumber });
          });
        });
        // 校验鞋款数量是否为空
        if (!shoeList.length) throw new Error(i18n.t('order-meeting.intention.select_at_least_one_unit'));

        // 弹出再次确认询问对话框
        const isConfirm = await this.$Dialog.dialogify({
          is: DialogSubmitConfirm,
        }, {
          props: {
            titleAlign: 'left',
            width: '467px',
            title: i18n.t('order-meeting.intention.warning'),
          },
        });
        if (isConfirm) {
          this.startLoading();

          // 根据接口整合数据
          form.wishUpdateShoeNumber = shoeList;
          await sumbitWishList(this.$route.query.workspaceId, form);
          this.$message.success(i18n.t('order-meeting.intention.order_submission_successful'));
          this.$router.push({
            name: 'IntentionPreview',
            query: {
              workspaceId: this.$route.query.workspaceId,
            },
          });
        }
      } catch (err) {
        this.$errHandler(err);
      } finally {
        this.endLoading();
      }
    },
  },
});
</script>

